.about-us {
  @include page-grid;

  &__title {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: span 3;

      @include on-desktop {
        grid-column: span 5;
      }
    }
  }

  &__desc {
    margin: 0;
    font-size: 23px;
    line-height: 140%;
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: span 3;

      @include on-desktop {
        grid-column: span 6;
      }
    }
  }
}
